import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Event from '@material-ui/icons/ListAlt';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { mainWindow } from '../App'
import * as tableActions from '../redux/actions/table'
import * as userActions from '../redux/actions/user'
import * as appActions from '../redux/actions/app'


const drawerWidth = 240;

const styles = theme => ({
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    toolbar: theme.mixins.toolbar,
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
    },
});

const MyDrawer = React.memo(
    (props) =>{
        const { drawer } = props.app;
        const { status } = props.user;
        const { showDrawer } = props.appActions;
        const { getData, setSelected } = props.tableActions;
        const menuItems = [
            {
                role: ['admin'],
                'name': 'блог',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Блог'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Блог'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'машина',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Машина'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Машина'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'организатор',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Организатор'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Организатор'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'план',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'План'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'План'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'точка',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Точка'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Точка'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'цена',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Цена'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Цена'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'реализатор',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Реализатор'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Реализатор'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'регион',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Регион'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Регион'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin'],
                'name': 'завсклада',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Завсклада'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Завсклада'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin', 'организатор', 'завсклада'],
                'name': 'накладная на пустую тару',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Накладная на пустую тару'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Накладная на пустую тару'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin', 'организатор', 'завсклада'],
                'name': 'накладная склад №1',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Накладная склад №1'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Накладная склад №1'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin', 'организатор', 'завсклада'],
                'name': 'накладная склад №2',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Накладная склад №2'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Накладная склад №2'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin', 'организатор', 'завсклада'],
                'name': 'накладная на вечерний возврат',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Накладная на вечерний возврат'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Накладная на вечерний возврат'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin', 'организатор'],
                'name': 'отчет организатора',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Отчет организатора'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Отчет организатора'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            },
            {
                role: ['admin', 'организатор', 'реализатор'],
                'name': 'отчет реализатора',
                'element'
                    :
                    <div>
                        <ListItem button key={'event'} onClick={()=>{props.history.push('/');showDrawer(false); setSearchItem(''); getData({search: '', sort: '', page: 0, name: 'Отчет реализатора'}); setSelected(-1)}}>
                            <ListItemIcon><Event /></ListItemIcon>
                            <ListItemText primary={'Отчет реализатора'} />
                        </ListItem>
                        <Divider variant='inset'/>
                    </div>,
            }
        ]
        const { authenticated } = props.user;
        let [searchItem, setSearchItem] = useState('');
        let handleChangeText =  (event) => {
            setSearchItem(event.target.value)
        };
        const { classes } = props;
        let variant= ''
        if(mainWindow.current.offsetWidth>800)
            variant= 'permanent'
        return (
            <div>
                <Drawer
                    variant= {variant}
                    className={classes.drawer}
                    open={drawer}
                    onClose={()=>showDrawer(false)}
                    classes={{paper: classes.drawerPaper,}}
                >
                    {
                        mainWindow.current.offsetWidth>800?
                            <div className={classes.toolbar}/>
                        :
                            null
                    }
                    {authenticated?
                        <List>
                            <ListItem button key={'search'}>
                                <TextField
                                    id='standard-search'
                                    label='Поиск'
                                    type='search'
                                    className={classes.textField}
                                    margin='normal'
                                    value={searchItem}
                                    onChange={handleChangeText}
                                />
                            </ListItem>
                            <Divider/>
                            {
                                menuItems.map((element) => {
                                    if (status.status==='active'&&(element.role.includes(status.role))) {
                                        if(searchItem!==''){
                                            if(element.name.includes(searchItem.toLowerCase()))
                                                return(
                                                        element.element
                                                )
                                        } else
                                            return(element.element)
                                    }
                                })
                            }
                        </List>
                        :
                        null
                    }
                </Drawer>
            </div>
        );
    }
)

function mapStateToProps (state) {
    return {
        app: state.app,
        user: state.user
    }
}

function mapDispatchToProps(dispatch) {
    return {
        appActions: bindActionCreators(appActions, dispatch),
        tableActions: bindActionCreators(tableActions, dispatch),
        userActions: bindActionCreators(userActions, dispatch),
    }
}

MyDrawer.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default  connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(MyDrawer));